.App-footer {
  display: flex;
  height: 100vh;
  flex-direction: column;
  justify-content: space-between;
  padding: 1em;
  background-color: #fff;
}

.App-footer img {
  cursor: pointer;
  width: 30px;
  margin: 0 auto;
}

.App-footer figure {
  margin: 0 auto;
}

.App-footer .cart span {
  color: #fff;
  text-align: center;

  font-size: 0.75rem;
  display: inline-block;
  width: 18px;
  height: 18px;
  margin-left: -1em;
  padding: 0.2em 0;
  background-color: #f16161;
  border-radius: 50%;
}

.App-footer p {
  display: flex;
  font-weight: 500;
  transform: rotate(-90deg);
  padding-left: 1em;
}

.App-footer p span {
  margin-left: 0.5em;
}

@media (max-width: 50em) {
  .App-footer {
    flex-direction: row;
    width: 100%;
    height: 8vh;
  }

  .App-footer img,
  .App-footer figure {
    margin: auto 0;
  }

  .App-footer p {
    transform: rotate(0);
    margin: auto 0;
    padding-left: 0;
  }
}

@media (max-width: 30em) {
  .App-footer {
    position: fixed;
    bottom: 0;
  }
}
