.Shop {
  display: flex;
  overflow: hidden;
}

.Shop .Single-product {
  width: 50%;
  height: 100vh;
}

.Shop .Products {
  display: flex;
  flex-wrap: wrap;
  width: 50%;
  height: 100vh;
}

.Shop .Products div {
  width: 50%;
}

.Shop .Products .Card,
.Shop .Single-product {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 40%;
  padding: 2em;
}

.Shop .Single-product {
  padding: 3em;
  height: 100vh;
  background-color: rgba(217, 216, 210, 0.5);
}

.Shop .Products .Card header,
.Shop .Single-product header {
  display: flex;
  justify-content: space-between;
}

.Shop .Products .Card h3,
.Shop .Single-product h3 {
  display: flex;
  font-size: 0.75rem;
  width: 60px;
  height: 60px;
  /* margin-bottom: -2em; */
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 50%;
}

.Shop .Single-product h3 {
  font-size: 1rem;
  width: 100px;
  height: 100px;
  margin: 0;
}

.Shop .Products .Card h3 span,
.Shop .Single-product h3 span {
  color: #ffffff;
  margin: auto;
}

.Shop .Products .Card header > span,
.Shop .Single-product header > span {
  cursor: pointer;
  display: flex;
  width: 28px;
  height: 28px;
  margin: auto 0;
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  overflow: visible;
}

.Shop .Products .Card header img,
.Shop .Single-product header img {
  width: 18px;
  margin: auto;
}

.Shop .Products .Card figure,
.Shop .Single-product figure {
  margin: 0 auto;
}

/* .Shop .Single-product figure  {
  
} */

.Shop .Products .Card h4,
.Shop .Single-product footer {
  font-weight: normal;
  color: #ffffff;
  height: 15%;
  margin: -1em auto 0;
}

.Shop .Single-product footer {
  color: #323742;
  width: 40%;
  position: absolute;
  bottom: 2em;
  right: 3em;
}

.Shop .Single-product figure img {
  transform: rotate(-35deg);
}

.Shop .Products .Pagination {
  display: flex;
  width: 100%;
  height: 140px;
}

.Shop .Products .Pagination li {
  display: flex;
  justify-content: space-evenly;
  width: 50%;
  padding: 2em;
}

.Shop .Products .Pagination li:first-of-type > span {
  display: flex;
  flex-direction: column;
  font-size: 1.8rem;
}

.Shop .Products .Pagination li:first-of-type > span span:nth-child(1) {
  margin-left: -0.8em;
  margin-bottom: -0.4em;
}

.Shop .Products .Pagination li:first-of-type > span span:nth-child(2) {
  color: #f2f6f3;
  transform: rotate(25deg);
}

.Shop .Products .Pagination li:first-of-type > span span:nth-child(3) {
  margin-top: -0.5em;
  padding-left: 0.5em;
}

.Shop .Products .Pagination li:last-of-type {
  font-style: italic;
  font-weight: 600;
  display: flex;
  text-align: center;
  padding: 2em 6em;
  background-color: #f2f6f3;
}

.Shop .Products .Pagination li:last-of-type span {
  margin: auto;
}

.Shop .Products .Pagination li:last-of-type span:first-of-type {
  color: rgba(0, 0, 0, 0.1);
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
  padding-top: 0.5em;
  padding-bottom: 0.5em;
}

@media (max-width: 50em) {
  .Shop {
    flex-direction: column;
  }

  .Shop .Single-product {
    width: 100%;
    height: 50vh;
  }

  .Shop .Single-product footer {
    position: absolute;
    /* bottom: 0; */
    right: 1em;
  }

  .Shop .Single-product figure {
    height: auto;
    margin-top: -4em;
    margin-left: -1em;
  }

  .Shop .Single-product figure img {
    height: 80%;
  }

  .Shop .Products {
    width: 100%;
    height: auto;
  }

  .Shop .Products .Card {
    width: 25%;
    height: 24vh;
  }

  .Shop .Products .Card h3 {
    margin-bottom: 0;
  }

  .Shop .Products .Card figure {
    margin: 1em auto;
  }

  .Shop .Products .Card h4,
  .Shop .Single-product h4 {
    margin: 0 auto 0;
  }

  .Shop .Products .Pagination {
    width: 100%;
    height: 10vh;
  }
}

@media (max-width: 30em) {
  .Shop .Single-product {
    position: fixed;
    padding: 1.5em;
    background-color: #f2f6f3;
    z-index: 1;
  }

  .Shop .Single-product h3 {
    width: 80px;
    height: 80px;
  }

  .Shop .Single-product figure {
    height: 70%;
    margin-top: -2em;
  }

  .Shop .Single-product figure img {
    height: 100%;
  }

  .Shop .Single-product footer {
    font-size: 0.875rem;
  }

  .Shop .Products {
    margin-top: 89%;
  }

  .Shop .Products .Card {
    width: 50%;
    height: 180px;
    padding: 1em;
  }

  .Shop .Products .Card h4 {
    display: none;
  }

  .Shop .Products .Pagination li:last-of-type {
    padding: 1em 2.5em;
  }

  .Shop .Products .Pagination {
    height: 100px;
  }

  .Shop .Products .Pagination li {
    padding: 1em;
  }
}
