.App {
  display: flex;
  height: 100vh;
  overflow: hidden;
}

.App-header {
  width: 80px;
  background-color: aliceblue;
}

.App-main {
  width: 100%;
}

.App-footer {
  width: 80px;
  background-color: aliceblue;
}

@media (max-width: 50em) {
  .App {
    flex-direction: column;
  }

  .App-header {
    width: 100%;
    height: 80px;
  }
}

@media (max-width: 30em) {
  .App {
    height: auto;
  }
  .App-main {
    position: relative;
    margin: 60px 0;
  }
}
