.App-header {
  display: flex;
  height: 100vh;
  flex-direction: column;
  justify-content: space-between;
  padding: 1em;
  background-color: #fff;
  z-index: 1;
  background: url("../../assets/bg-header.svg") no-repeat center center;
  background-size: cover;
}

.App-header h1 img {
  height: 48px;
}

.App-header nav {
  display: flex;
  /* padding: 1em; */
}

.App-header nav > img {
  cursor: pointer;
  width: 30px;
  margin: 0 auto;
}

.App-header nav li {
  cursor: pointer;
  display: inline-block;
  margin-right: 100%;
  font-weight: 900;
  font-size: 1.5rem;
  margin-bottom: 1em;
}

.App-header nav .active {
  color: rgba(0, 0, 0, 0.1);
}

/* .App-header nav ul {
  display: flex;
  flex-direction: column;
} */

.App-header nav li:hover {
  color: rgba(0, 0, 0, 0.1);
  padding: 0.3em;
  background-color: #fff;
  transition: ease 1s;
}

.App-header .social-media {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.App-header .social-media span {
  font-weight: 500;
  transform: rotate(-90deg);
  padding-left: 1em;
}

.App-header .social-media img {
  height: 18px;
}

@media (max-width: 50em) {
  .App-header {
    flex-direction: row;
    height: 8vh;
  }

  header.add-class {
    width: 100% !important;
    flex-direction: column;
  }

  .App-header nav li {
    padding-right: 1em;
  }

  .App-header .social-media span {
    transform: rotate(0deg);
    width: 30%;
    padding: 0;
    margin: auto;
  }
}

@media (max-width: 30em) {
  .App-header {
    position: fixed;
    padding: 1em 2em !important;
    height: 60px;
    z-index: 10;
  }

  .App-header h1 img {
    height: 32px;
  }

  .App-header .social-media {
    display: none;
  }

  .App-header nav .close-menu {
    margin: auto 0 !important;
    /* margin-right: 2em; */
  }

  .App-header nav li {
    padding-right: 0;
    padding-left: 1em;
  }

  .App-header ul.add-class {
    display: flex;
  }
}
